@mixin btn ($type: regular, $height: 32px) {

    height: $height;
    border-radius: 3px;
    line-height: $height;
    padding: 0 16px;
    border: none;

    @if $type==light {
        background-color: $opc-white;
        color: $txt;

        &:hover {
            background-color: $opc-white-hov;
        }

    }

    @if $type==dark {
        background-color: $opc-black;
        color: $bg;

        &:hover {
            background-color: $opc-black-hov;
        }
    }
}

@mixin input($width: 100%) {
    outline: 2px solid #026AA7;
    border-radius: 3px;
    height: 32px;
    padding-inline-start: 4px;
    margin-bottom: 16px;
    width: $width;
    outline: 2px solid $clr1;
}

@mixin img-icon {
    width: 38px;
    height: 38px;
    border-radius: 50%;



}

@mixin menu-modal {
    .background-modal {
        background-color: $td-bg;
        text-align: center;
        width: 340px;
        height: 100%;
        position: fixed;
        z-index: 20;
        top: 44px;
        right: 0;
    }
}


// Responsiveness mixins

@mixin mobile-layout {
    @media (max-width: 400px) {
      @content;
    }
  }
  
  @mixin narrow-layout {
    @media (min-width: 400px) {
      @content;
    }
  }
  
  @mixin normal-layout {
    @media (min-width: 745px) {
      @content;
    }
  }
  
  @mixin wide-layout {
    @media (min-width: 830px) {
      @content;
    }
  }


